import Vue from 'vue'
import VueRouter from 'vue-router'

import { Toast } from 'vant'

// import Home from '../components/home/Home.vue'
const Home = () =>
  import(/* webpackChunkName: "home" */ '../components/home/Home.vue')

// import Login from '../components/login/member_login.vue'
const Login = () =>
  import(/* webpackChunkName: "login" */ '../components/login/member_login.vue')

// import Classify from '../components/classify/classify.vue'
const Classify = () =>
  import(/* webpackChunkName: "classify" */ '../components/classify/classify.vue')

// import Member from '../components/member/Member.vue'
const Member = () =>
  import(/* webpackChunkName: "member_order" */ '../components/member/Member.vue')

// import Order from '../components/order/Order.vue'
const Order = () =>
  import(/* webpackChunkName: "member_order" */ '../components/order/Order.vue')

// import Cart from '../components/cart/Cart.vue'
const Cart = () =>
  import(/* webpackChunkName: "cart" */ '../components/cart/Cart.vue')

// import Search from '../components/search/Search.vue'
const Search = () =>
  import(/* webpackChunkName: "search" */ '../components/search/Search.vue')

// import photoSharing from '../components/photo_sharing/photo_sharing.vue'
const photoSharing = () =>
  import(/* webpackChunkName: "photoSharing_details" */ '../components/photo_sharing/photo_sharing.vue')

// import photoDetails from '../components/photo_sharing/photo_details.vue'
const photoDetails = () =>
  import(/* webpackChunkName: "photoSharing_details" */ '../components/photo_sharing/photo_details.vue')

// import GoodsList from '../components/goods/GoodsList.vue'
const GoodsList = () =>
  import(/* webpackChunkName: "goodsdetail_desc_list" */ '../components/goods/GoodsList.vue')

// import GoodsDetails from '../components/goods/GoodsDetails.vue'
const GoodsDetails = () =>
  import(/* webpackChunkName: "goodsdetail_desc_list" */ '../components/goods/GoodsDetails.vue')

// import GoodsDesc from '../components/goods/goodsDesc.vue'
const GoodsDesc = () =>
  import(/* webpackChunkName: "goodsdetail_desc_list" */ '../components/goods/goodsDesc.vue')

// import New from '../components/news/news.vue'
const New = () =>
  import(/* webpackChunkName: "news_list" */ '../components/news/news.vue')

// import Newliist from '../components/news/Newslist.vue'
const Newliist = () =>
  import(/* webpackChunkName: "news_list" */ '../components/news/Newslist.vue')
const Pclogin = () =>
  import(/* webpackChunkName: "pc-login" */ '../components/pc_login/login.vue')
const Add = () =>
  import(/* webpackChunkName: "search-Add" */ '../components/search/Adds.vue')
// import GoodsComments from '../components/goods/GoodsComments.vue'
const GoodsComments = () =>
  import(/* webpackChunkName: "goodsdetail_desc_list_comment" */ '../components/goods/GoodsComments.vue')
const Map = () =>
  import(/* webpackChunkName: "map" */ '../components/map/Map.vue')

Vue.use(VueRouter)
const routes = [
  { path: '/', redirect: '/home' },
  { path: '/home', component: Home },
  { path: '/classify', component: Classify },
  { path: '/member', component: Member },
  { path: '/order', component: Order },
  { path: '/cart', component: Cart },
  { path: '/search', component: Search },
  { path: '/photo_sharing', component: photoSharing },
  { path: '/photo_details/:id', component: photoDetails, props: route => Object.assign({}, route.query, route.params) },
  { path: '/goods/list', component: GoodsList },
  { path: '/goods/detail/:id', component: GoodsDetails },
  { path: '/goodsdesc/:id', component: GoodsDesc },
  { path: '/login', component: Login },
  { path: '/news', component: New },
  { path: '/api/getnew/:id', component: Newliist },
  { path: '/pclogin', component: Pclogin },
  { path: '/Adds', component: Add },
  { path: '/goodscomments/:id', component: GoodsComments },
  { path: '/contact', component: Map }
]

const router = new VueRouter({
  routes
})

// 登录拦截，重定向到登录页面
router.beforeEach((to, from, next) => {
  if (to.path !== '/member') return next()
  const tokenStr = window.sessionStorage.getItem('token')
  if (!tokenStr) {
    Toast.fail('您还没有登录!')
    return next('/login')
  }
  next()
})
export default router
