import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    cartData: []
  },
  getters: {
    getIds (state) { // 获取id所组成的数组
      return state.cartData.map(item => item.id)
    }
  },
  mutations: {
    addToCart (state, item) { // 添加商品到购物车
      // console.log(state.cartData);
      let index = -1 // 判断商品是否已存在于购物车中
      state.cartData.some((curItem, curIndex) => {
        if (curItem.id === item.id) {
          index = curIndex
          return true
        } else {
          return false
        }
      })
      index === -1 ? (state.cartData.push(item))
        : (state.cartData[index].num += item.num)
      localStorage.setItem('cartData', JSON.stringify(state.cartData))
      localStorage.setItem('cartNum', item.num)
    },
    removeFromCart (state, index) { // 从购物车中删除商品
      state.cartData.splice(index, 1)
    }
  },
  actions: {},
  modules: {}
})
